@import "./breakpoints.scss";

.grid-container {
    margin-left: auto;
    margin-right: auto;
    max-width: 1240px;
    padding-left: 20px;
    padding-right: 20px;
    position: relative;
}

.row {
    margin-left: -20px;
    margin-right: -20px;
}

.clearfix:after,
.clearfix:before,
.grid-container:after,
.grid-container:before,
.row:after,
.row:before {
    clear: both;
    content: "";
    display: block;
    height: 0;
    overflow: hidden;
    width: 100%;
}
#global_container {
    overflow: hidden;
    position: relative;
}

