.content-icon-row {
    margin: 20px 0;
}

.content-icon-row .icon-wrapper {
    margin: 5px 50px 20px 30px;
}

.content-icon-row .icon-wrapper > .ico {
    display: block;
    margin: 0 auto;
}

.content-cover {
    background-color: #283545;
    height: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    top: 0;
    transition: opacity 0.25s ease 0s, height 0s ease 0.25s;
    width: 100%;
    z-index: 140;
}

